.navbar{
    width: 100%;
    display: block;
    padding: 1rem 0px;

    background-color: rgb(250, 250, 250);
    box-shadow: 1px 2px 15px 1px #353535;
    position: relative;
    z-index: 10;

    .wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.menu{
    display: none;
}

.navbar_list{
    list-style: none;
    display: flex;
}

.navbar_list_item{
    padding-left: 5rem;
    font-size: 1.2rem;

    &:first-child{
        padding-left: 0;
    }

    a{
        color: rgb(17, 17, 17);

        &:hover{
            color: rgb(0, 255, 42);
            transition: color .4s ease;
        }
    }
}

.navbar_logo{
    width: 100%;
    max-width: 2.4rem;
    height: auto;

    img{
        width: 100%;
        height: 100%;

       filter: contrast(0.8);

        &:hover{
            filter: contrast(2);
            transition: filter .5s ease;
        }
    }
}

.navbar_marks{
    width: 100%;
    height: auto;
    max-width: 32px;
    position: relative;

    img{
        width: 100%;
        height: 100%;

        &:hover{
            filter: invert(1);
            transition: filter .5s ease;
        }
    }
}

.navbar_marks_counter{
    position: absolute;
    top: -0.5rem;
    right: 0;

    width: 1rem;
    height: 1rem;
    background-color: white;
    border-radius: 50%;
    color:black
}

@media screen and (max-width: 420px) {
    .navbar_list{
        display: none;
    }

    .navbar{
        position: relative;

        .wrapper{
            justify-content: flex-start;
        }
    }

    .menu{
        display: block;
        width: 32px;
        height: 2px;
        margin-left: auto;
    }

    .burger_menu{
        position: relative;
        height: 32px;

        &::after{
            position: absolute;
            content: '';
            width: 16px;
            height: 1px;
            background-color: rgb(5, 5, 5);
        }

        &::before{
            position: absolute;
            content: '';
            width: 16px;
            height: 1px;
            background-color: rgb(5, 5, 5);
        }

        &::after{
            top:-6px;
        }

        &::before{
            top:6px;
        }

        span{
            display: block;
            width: 16px;
            height: 1px;
            background-color: rgb(5, 5, 5);
        }
    }

    .active_burger{
        &::after{
            top:0;
            transform: rotate(45deg);
            transition: transform .3s ease;
        }

        &::before{
            top:0;
            transform: rotate(-45deg);
            transition: transform .3s ease;
        }

        span{
            opacity: 0;
            transition: opacity .6s ease;
        }
    }
    
    .navbar{
        position: fixed;
        top: 0;
        z-index: 5;
    }


    .navbar_container{
        position: absolute;
        right: 0;
        top:100%;
        color:black;
        height: 100vh;
        background-color: #e7f5fcd4;;
        transition: right .4s ease;

        &.un_active{
          right: -1000px;  
        }

        a{
            color: black;
        }

        .active_burger{
            display: flex;
            flex-direction: column;

            .navbar_list_item{
                font-size: 2rem;
                padding: 2rem 2rem;
                text-align: center;
            }
        }
    }

  }
  