.sort{
    margin-bottom: 6.25rem;
    text-align: center;
}

.sort_title{
    width: 60%;
    padding: 0.75rem 1.25rem;
    box-sizing: border-box;
}

.news_sorted{
    text-align: center;
    margin-bottom: 3.125rem;
}

.news_select {
    background-color: #3271f7;
    color: white;
    padding: 0.375rem 3.125rem;
    border: none;
    font-size: 1.2rem;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
    -webkit-appearance: button;
    appearance: button;
    outline: none;
}

.news_select::before {
    content: "\f13a";
    font-family: FontAwesome;
    position: absolute;
    top: 0;
    right: 0;
    width: 20%;
    height: 100%;
    text-align: center;
    font-size: 1.75rem;
    line-height: 7.5rem;
    color: rgba(255, 255, 255, 0.5);
    background-color: rgba(255, 255, 255, 0.1);
    pointer-events: none;

    padding-right: 0.625rem;
}

.news_select:hover::before {
    color: rgba(255, 255, 255, 0.6);
    background-color: rgba(255, 255, 255, 0.2);
}

.news_select option {
    padding: 1.875rem;
    background-color: white;
    color: black;
}

.http_sorted{

    margin: 5rem 0;

    .wrapper{
        margin: 5rem 0;   
    }
}

.http_sorted_item{
    display: flex;
    align-items: center;
    margin-bottom: 1.875rem;
}

.http_input{
    width: 100%;
}

.http_form{
    width: 100%;
}

@media screen and (max-width: 460px) {
    .http_sorted_item{
       display: block;
    }

    .http_form{
        margin-top: 1rem;
    }
  }
  