.pagination{
    max-width: 100%;
    padding-bottom: 2rem;
    overflow-x: hidden;
}

.pagination_list{
    list-style: none;
    display: flex;
}

.pagination_list_item{
    padding: 0.5rem 2rem;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    background-color: #8bff0038;

    &:hover{
        background-color: antiquewhite;
        transition: background-color .3s ease;
    }
}