.home{
    width: 100%;
    height: 100vh;

    background-image: url("../imgs/Home/bacground_home.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    .container{
        height: 100%;
    }

    .wrapper{
        display: flex;
        flex-direction: column;

        height: 100%;
        justify-content: center;

        color: rgb(12, 250, 12);
    }
}

.home_title{
    font-size: 4rem;
    line-height: 4rem;
}

.home_subtitle{
    font-size: 1.5rem;
    margin-top: 20px;
}

.home .home_btn{
    margin-top: 2rem;
    padding: 1rem 5rem;
    font-size: 1.5rem;
}