.details{
    margin-top: 5rem;
}

.details_date{
    font-size: 0.85rem;
    opacity: 0.7;
    padding-left: 0.2rem;
}

.details_title{
    font-size: 3rem;
    line-height: 3rem;
    font-weight: 600;
}

.details_description{
    margin-top: 4rem;
    font-size: 1.2rem;
    line-height: 2rem;
    text-align: justify;
    font-family: monospace;
}

.details_footer{
    margin-top: 3rem;
    text-align: center;
}

.sort_description{
    margin-top: 1rem;
}

.detail_post{
    padding-bottom: 5rem;

    &:last-of-type{
        hr{
            display: none;
        }
    }
}

.details_category{
    margin-top: 6.25rem;

    .wrapper{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .post_btn{
        margin-top: 1rem;

        font-size: 0.8rem;
        padding: 0.5rem 2rem;
    }
}

.category{
    padding: 1.25rem;  
    min-width: 40%;
    max-width: 40%;
    box-shadow: -1px -1px 12px -8px black;
    margin: 0 1rem 2rem 1rem;

}

.category_post_title{
    font-size: 1.2rem;
    font-weight: 600;
}

.category_post_category{
    margin-top: 0.2rem;
    font-weight: lighter;
}

.details_img{
    width: 100%;
    margin-top: 2rem;
    text-align: center;

    img{
        max-width: 100%;
        height: auto;
    }
}

@media screen and (max-width: 950px) {
    .details_category{
    
        .wrapper{
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
        }
    }

    .category{
        margin-left: 0;
        margin-right: 0;
    }
}

@media screen and (max-width: 720px) {
    .category{
        min-width: 80%;
        max-width: 80%;
    }
}

@media screen and (max-width: 480px) {

    .category{
        min-width: 80%;
        max-width: 80%;

        .post_btn{
            font-size: 1.5rem;
            padding: 1rem 2rem;
        }
    }

    .category_post_title{
        font-size: 1.5rem;
        font-weight: 600;
    }

    .big_post_btn{

        font-size: 1.5rem;
        padding: 1.2rem 1.5rem;
        
    }
}

