.posts{

    margin-top: 100px;

    .wrapper{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
}

.posts_title{
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 9.375rem;
}

.post{
    position: relative;
    min-height: 42rem;

    max-width: 40%;
    padding: 1.5625rem 1.5625rem 1.125rem 1.5625rem;
    margin-bottom: 30px;

    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    background: linear-gradient(0.25turn, #7472ff4a, #ebf8e1, #0098f15e);
}

.post_title{
    font-size: 1.5rem;
    font-weight: 600;

    padding-bottom: 2px;
    height: 4rem;
    overflow-y: hidden;
}

.post_date{
    opacity: 0.5;
}

.post_description{
    max-height: 9.6875rem;
    overflow-y: hidden;
    margin-top: 1rem;

    font-size: 1rem;
    text-align: justify;
}

.post_img{
    width: 100%;
    margin-top: 1rem;
    text-align: center;

    img{
        max-width: 100%;
        max-height: 20rem;
    }
}

.post_url{
    font-size: 0.75rem;
    opacity: 0.7;
    padding: 0.5rem 0 1rem 0;

    &:hover{
        opacity: 1;
        transition: opacity .3s ease;
    }
}

.post_footer{
    position: absolute;
    bottom: 0;

    padding-bottom: 2rem;
    width: 90%;
    display: flex;
    justify-content: space-between;

    .more{
        padding: 1rem 3rem;
    }
}
    

.post_btn{
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #07ff77 0, #0e3d64 100%);
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono",monospace;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  padding-left: 1rem;
  padding-right: 1rem;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 1rem;
  padding: 0.75rem;
}

.post_btn:focus {
  box-shadow: #2c7bf1 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.post_btn:hover {
  box-shadow: rgba(47, 43, 100, 0.4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
  transform: translateY(-2px);
}

.post_btn:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}

.post_url{
    max-width: 100%;
    overflow-x: hidden;
}

@media screen and (max-width: 1000px) {

    .post_btn{
        padding: 0.7rem;
    }

    .post_footer{
        .more{
            padding: 1rem 2.5rem;
        }
    }
}

@media screen and (max-width: 630px) {
    .posts{
    
        .wrapper{
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    .post{
        min-height: 100%;
        padding-bottom: 1.5rem;
    }
    
    .post_btn{
        font-size: 1rem;
        padding: 1rem;
        margin-bottom: 1rem;
    }

    .post_footer{
        display: flex;
        justify-content: space-between;

        padding: 0rem;
        position: relative;
        margin: 2rem auto;

        .more{
            padding: 1rem 3.7rem;
            font-size: 1rem;
        }
    }
}

@media screen and (max-width: 630px) {

    .post{
        max-width: 80%;
    }

}

@media screen and (max-width: 420px) { 
    .post{
        max-width: 95%;
    }
  }